<template>
  <div class="user-detail">
    <div class="user-detail-top">基本信息</div>
    <div class="user-detail-group">
      <div class="user-icon">
        <span>头像</span>
        <img :src="userInfo.user_avatar" alt="">
      </div>
      <div class="user-item">
        <span>手机</span>
        <span>{{ userInfo.user_phone || '暂无' }}</span>
      </div>
      <div class="user-item">
        <span>账号</span>
        <span>{{ userInfo.user_name || '暂无' }}</span>
      </div>
      <div class="user-item">
        <span>昵称</span>
        <span>{{ userInfo.user_nickname || '暂无' }}</span>
      </div>
      <div class="user-item">
        <span>余额</span>
        <span>{{ userInfo.user_balance || 0 }} 元</span>
      </div>
      <div class="user-item">
        <span>性别</span>
        <span>{{ userInfo.user_sex || '暂无' }}</span>
      </div>

      <div class="user-item">
        <span>凭证</span>
        <el-image
          v-if="userInfo.user_voucher"
          style="width: 100px; height: 100px"
          :src="userInfo.user_voucher"
          :preview-src-list="[userInfo.user_voucher]"
        />
        <span v-else>暂无</span>
      </div>
      <div class="button">
        <el-button type="success" @click="dialogVisible=true">充值</el-button>
      </div>
    </div>
    <!-- 充值模态框 -->
    <el-dialog
      title="余额充值"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose">
      <el-input v-model="balance" type="number" placeholder="请输入金额"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="sumbit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState} from 'vuex';
  import {mapActions} from 'vuex'
  import {changeBalance} from './../../../api/index';

  export default {
    data() {
      return {
        dialogVisible: false,
        balance: '', // 余额
      }
    },
    watch: {
      dialogVisible() {
        this.balance = ''
      }
    },
    computed: {
        ...mapState(["userInfo"])
    },
    methods: {
      sumbit() {
        if (!this.balance) {
          this.$message.error('请输入金额')
          return
        }
        const params = {
          id: this.userInfo.id,
          balance: this.balance,
          type: 1
        }
        changeBalance(params).then(res => {
          if (res.success_code === 200) {
            this.$store.dispatch('getUserInfo',{user_id: this.userInfo.id})
            this.dialogVisible = false
            this.$message.success('充值成功')
          } else {
            this.$message.error('充值失败，请稍后重试')
          }
        })
      },
      handleClose() {
        this.dialogVisible = false
      }
    },
  }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  .user-detail
    width 70%
    height 100%
    margin 20px auto
    .user-detail-top
      width 100%
      height 60px
      line-height 60px
      padding-left 10px
      font-weight bold
    .user-detail-group
      .user-icon
        height 60px
        padding 0 10px
        background-color #fff
        border-bottom: 1px solid #e0e0e0
        display flex
        justify-content space-between
        align-items center
        img
          width 50px
          height 50px
          border-radius 50%
      .user-item
        min-height 50px
        padding 10px
        background-color #fff
        border-bottom: 1px solid #e0e0e0
        display flex
        justify-content space-between
        align-items center
      .button
        padding-top 15px
        display flex
        justify-content flex-end
    .right-title-color
      color #999
      font-size 14px
</style>




